.intro {
    background: url(../img/intro-min.jpg) no-repeat;
    height: 554px;
    background-size: cover;
    background-position: center center;
    position: relative;
    &-slider {
        overflow: hidden;
    }
    &-wrap {
        height: 554px;
        display: flex;
        justify-content: center;
        .sm-block({
            align-items: center;
        });
    }
    &-text {
        margin-top: 175px;
        color: #fff;
        text-align: center;
        .sm-block({
            margin-top: 0px;
        });
        &__h1 {
            .compact();
            font-size: 79px;
            .sm-block({
                font-size: 60px;
            });
        }
        &__desc {
            .compact();
            font-size: 38px;
            .sm-block({
                font-size: 32px;
            });
            .xxs-block({
                font-size: 28px;
            });
        }
        &__btn {
            margin-top: 25px;
            .helio();
            display: inline-block;
            background-color: @orange;
            border: 1px solid @orange;
            font-size: 36px;
            letter-spacing: 1.8px;
            padding: 14px 46px;
            border-radius: 5px;
            transition: .3s all;
            .sm-block({
                font-size: 32px;
                padding: 10px 30px;
            });
            &:hover {
                color: @orange;
                background-color: transparent;
            }
        }
    }
    &-right {
        position: absolute;
        right: 25px;
        top: 50%;
        transform: translate(0,-50%);
        width: 228px;
        z-index: 11;
        .lg-block({
            display: none;
        });
        &--top {
            background-color: #5d8d20;
            padding: 0 15px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            height: 53px;
        }
        &__h4 {
            .helio();
            font-size: 36px;
            letter-spacing: 1.8px;
            color: #fff;
        }
        &--content {
            min-height: 119px;
            padding: 15px;
            background-color: rgba(0,0,0,0.5);
            position: absolute;
            bottom: 0;
            width: 100%;
        }
        &__h3 {
            .helio();
            color: #fff;
            font-size: 27px;
            letter-spacing: 1.35px;
        }
        &__time {
            margin-top: 5px;
            .helio();
            color: @orange;
            font-size: 27px;
            letter-spacing: 1.35px;
            position: relative;
            padding-left: 45px;
            &::after {
                content: '';
                position: absolute;
                top: 0; left: 0px;
                background-image: url(../img/circle_orange.png);
                width: 33px;
                height: 33px;
            }
        }
        &--bottom {
            background: url(../img/intro-left-min.jpg) no-repeat;
            height: 315px;
            position: relative;
        }
    }
}
.slick-dots {
    bottom: 35px;
    .sm-block({
        display: none !important;
    });
}
.slick-dots li {
    position: relative;
    display: inline-block;
    border-radius: 50%;
    border: none;
    background-color: #c3c5c8;
    width: 12px;
    height: 12px;
    margin: 0 4px;
    cursor: pointer;
}
.slick-dots li.slick-active {
    background-color: #5d8d21;
}
.slick-dots li button::before {
    content: '';
}
.slick-dotted.slick-slider {
    margin: 0;
}