.header {
    margin-top: 50px;
    .md-block({
        margin-top: 20px;
    });
    .xs-block({
        margin-top: 10px;
    });
    &-top {
        &--wrap {
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            .md-block({
                align-items: center;
            });
        }
    }
    &--left {
        display: flex;
        position: relative;
        .md-block({
            align-items: center;
        });
        &__logo {
            position: absolute;
            z-index: 99;
            .lg-block({
               top: -15px;
            });
            .md-block({
                position: static;
            });
            img {
                .lg-block({
                    height: 170px;
                });
                .md-block({
                    height: 120px;
                });
                .sm-block({
                    height: 90px
                });
            }
        }
        &__title {
            margin-left: 250px;
            font-size: 43px;
            color: @orange;
            .bold();
            .lg-block({
                margin-left: 170px;
            });
            .md-block({
                margin-left: 15px;
                font-size: 32px;
            });
            .sm-block({
               font-size: 24px;
            });
            .xs-block({
                display: none;
            });
        }
    }
    &--right {
        display: flex;
    }
    &--news {
        .sm-block({
            display: none;
        });
        margin-top: 25px;
        p {
            color: #504f53;
            font-size: 17px;
        }
    }
    &--social {
        margin-top: 10px;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
    }
    &--tel {
        margin-left: 34px;
        &__top {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
        &__bottom {
            margin-top: 10px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            p {
                color: #504f53;
                font-size: 17px;
                .xs-block({
                    display: none;
                });
            }
        }
        &__callback {
            display: block;
            margin-left: 10px;
            font-size: 18px;
            background-color: #5d8d20;
            border: 1px solid #5d8d20;
            color: #fff;
            padding: 3px 10px;
            border-radius: 5px;
            transition: .3s all;
            &:hover {
                color: #5d8d20;
                background-color: #fff;
            }
            .xs-block({
                margin-left: auto;
            });
        }
    }
    &--number {
        display: flex;
        flex-direction: column;
        &__item {
            color: #5d8d21;
            font-size: 22px;
        }
        .xs-block({
            margin-right: 20px;
        });
    }
    &-bottom {
        margin-top: 10px;
        height: 57px;
        background-color: @orange;
        &--wrap {
            display: flex;
            align-items: center;
            justify-content: space-between;
            height: 57px;
        }
    }
    &--menu {
        margin-left: 232px;
        .lg-block({
            margin-left: 152px;
        });
        .md-block({
            margin-left: 0px;
        });
        &__ul {
            display: flex;
            .md-block({
            flex-direction: column;
            });
        }
        &__li {
            margin: 0 18px;
            .lg-block({
                margin: 0 10px;
            });
            .md-block({
                margin: 10px 0;
            });
        }
        &__a {
            .helio();
            font-size: 36px;
            color: #fefeff;
            line-height: 40px;
            .lg-block({
                font-size: 28px;
            });
            .md-block({
                font-size: 24px;
                color: #504f53;
            });
            &::after {
                display:block;
                content: '';
                border-bottom: 2px solid #fefeff;   
                transform: scaleX(0);    
                transform-origin: 100% 50%;
                transition: transform 450ms ease-in-out;
            }
            &:hover::after {
                transform: scaleX(1);
                transform-origin: 0% 50%;
            }
        }
    }
    &--search {
        position: relative;
        &__submit {
            cursor: pointer;
            position: absolute;
            top: 5px; right: 8px;
            background-image: url(../img/search.png);
            width: 22px; height: 22px;  
        }
        &__input {
            display: block;
            width: 410px;
            height: 31px;
            color: #aeafb2;
            font-size: 21px;
            padding: 15px 6px;
            &::placeholder {
                color: #aeafb2;
                font-size: 21px;
                .lg-block({
                    font-size: 16px;
                });
            }
            .lg-block({
                width: 240px;
                height: 25px;
                font-size: 16px;
            });
        }
    }
}
#menu {
    .md-block({
        position: absolute;
        top: -1000px; left: 0;
        background-color: #fff;
        z-index: 100;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: .3s all;
        overflow: hidden;
    });
    &.active {
         top: 0;   
    }
}
.btn_mnu {
    display: none;
    .md-block({
        display: flex;
    });
    width: 50px;
    height: 50px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    cursor: pointer;
    position: relative;
    z-index: 101;
}
.btn_row {
    height: 2px;
    width: 60%;
    margin: 2px 0;
    background-color: #fff;
    position: relative;
    transition: all .5s;
}
.btn_mnu.active .btn_row:first-child {
    transform: rotate(-135deg);
    top: 6px;
    background-color: #000;
}
.btn_mnu.active .btn_row:last-child {
    transform: rotate(-225deg);
    top: -6px;
    background-color: #000;
}
.btn_mnu.active .btn_row:nth-child(2) {
    opacity: 0;
}

@media screen and (max-width:1400px) {
    .header--menu {
        margin-left: 232px;
        &__ul {
            display: flex;
        }
        &__li {
            margin: 0 15px;
        }
        &__a {
            .helio();
            font-size: 31px;
            color: #fefeff;
            line-height: 40px;
        }
    }
    .header--search__input {
        width: 300px;
    }
}