.move {
    &_top {
        display: flex;
        align-items: center;
        height: 115px;
        .xs-block({
            height: inherit;
            margin: 25px;
        });
        .xxs-block({
            flex-direction: column;
        });
    }
    &_h2 {
        color: #504f53;
        font-size: 48px;
        .xs-block({
            font-size: 42px;
        });
    }
    &_btn {
        font-size: 28px;
        line-height: 28px;
        border-radius: 5px;
        border: 2px solid @orange;
        color: @orange;
        padding: 21px;
        margin-left: 30px;
        transition: .3s all;
        .xs-block({
            font-size: 24px;
            padding: 10px;
        });
        .xxs-block({
            margin: 10px 0 0;
            font-size: 21px;
        });
        &:hover {
            background-color: @orange;
            color: #fff;
        }
    }
    &_slider {
        overflow: hidden;
        position: relative;
        .slick-prev {
            left: 0;
            background-image: url(../img/arrow.png);
            width: 55px;
            height: 55px;
            z-index: 99;
            top: 35%;
            transform: rotate(0deg);
            &::before {
                content: '';
            }
        }
        .slick-next {
            right: 0;
            background-image: url(../img/arrow.png);
            transform: rotate(-180deg);
            width: 55px;
            height: 55px;
            z-index: 99;
            top: 35%;
            &::before {
                content: '';
            }
        }
    }
    &_item {
        margin: 0 12.5px;
        &--top {
            background: url(../img/tour-item-min.jpg) no-repeat;
            background-size: cover;
            background-position: center center;
            height: 290px;
        }
        &__h3 {
            .helio();
            padding: 12px 0;
            padding-left: 12px;
            font-size: 31px;
            letter-spacing: 1.5px;
            color: #fff;
            background-color: rgba(0,0,0,.5);
        }
        &--center {
            background-color: #4f8934;
            min-height: 47px;
            display: flex;
            align-items: center; 
            img {
                margin-left: 17px;
            }
            p {
                margin-left: 24px;
                color: #fff;
                font-size: 31px;
                .helio();
            }
        }
        &--bottom {
            background-color: #ecedef;
            display: flex;
            justify-content: space-between;
            align-items: center;
            min-height: 95px;
        }
        &__time {
            display: flex;
            margin-left: 14px;
            align-items: center;
            p {
                margin-left: 14px;
                .helio();
                color: #000;
                font-size: 31px;
                line-height: 1;
                letter-spacing: 1.55px;
                .xs-block({
                    font-size: 24px;
                });
                .xxs-block({
                    font-size: 21px;
                });
            }
        }
        &__btn {
            display: inline-block;
            padding: 12px;
            margin-right: 14px;
            font-size: 36px;
            line-height: 1;
            letter-spacing: 1.8px;
            color: #fff;
            border: 1px solid @orange;
            background-color: @orange;
            transition: .3s all;
            &:hover {
                background-color: transparent;
                color:  @orange;
            }
            .xs-block({
                font-size: 24px;
                padding: 10px;
            });
            .xxs-block({
                font-size: 21px;
            });
        }
    }
}

.email {
    margin-top: 40px;
    background-color: @orange;
    min-height: 260px;
    color: #504f53;
    padding-top: 40px;
    .sm-block({
        padding: 40px 0 30px;
        min-height: inherit;
    });
    &_wrap {
        display: flex;
        justify-content: space-between;
        min-height: 260px;
        .sm-block({
            min-height: inherit;
        });
    }
    &_right {    
        .lg-block({
            display: none;
        });
    }
    &_h2 {
        font-size: 58px;
        line-height: 60px;
        .xs-block({
            font-size: 50px;
            text-align: center;
        });
    }
    &_desc {
        font-size: 24px;
        .sm-block({
            font-size: 18px;
        });
        .xs-block({
            text-align: center;
        });
    }
    &_form {
        margin-top: 15px;
        display: flex;
        flex-wrap: wrap;
        .xs-block({
            flex-direction: column;
        });
    }
    &_input {
        display: block;
        width: 253px;
        height: 50px;   
        border: 1px solid #69676c;
        color: #68676c;
        font-size: 24px;
        padding: 15px;
        .sm-block({
            width: 30%;
        });
        .xs-block({
            margin: 5px auto;
            width: 80%;
        });
        &::placeholder {
            color: #68676c;
            font-size: 24px;
        }
        &:nth-child(2) {
            margin-left: 12px;
            .xs-block({
                margin-left: auto;
            });
        }
    }
    &_submit {
        display: block;
        font-family: 'FucoExtraAP';
        background-color: transparent;
        margin-left: 12px;
        font-size: 24px;
        width: 148px;
        height: 50px;
        color: #fff;
        border: 1px solid #69676c;
        cursor: pointer;
        transition: .3s all;
        .sm-block({
            width: 25%;
        });
        .xs-block({
            margin: 10px auto 0;
            text-align: center;
            width: 50%;
        });
        &:hover {
            background-color:#69676c;
            color: @orange;
        }
    }
    &_label {
        display: flex;
        width: 100%;
        margin-top: 10px;
        p {
            margin-left: 5px;
            color: #000;
            .xs-block({
                font-size: 12px;
            });
        }
    }
}

.checkbox {
    position: relative;
    cursor: pointer;
}
.checkbox .label {
    margin-top: 3px;
    position: relative;
    display: block;
    width: 12px;
    height: 12px;
    border: 1px solid #69676c;
    background-color: #fff;
    border-radius: 100%;
}
.checkbox .label:after {
    content: '';
    position: absolute;
    top: 2px;
    left: 2px;
    width: 6px;
    height: 6px;
    border-radius: 100%;
    background: #69676c;
    transform: scale(0);
    transition: all 0.2s ease;
    opacity: 0.1;
    pointer-events: none;
}
.checkbox:hover .label:after {
    transform: scale(4.2);
}
input[type="checkbox"]:checked + .label {
    border-color: #69676c;
    animation: jelly 0.6s ease forwards;
}
input[type="checkbox"]:checked + .label:after {
    transform: scale(1);
    transition: all 0.2s ease;
    opacity: 1;
}
.hidden {
    display: none;
}

.tour {
    margin-top: 40px;
    &_h2 {
        color: #504f53;
        font-size: 58px;
        line-height: 58px;
        .xs-block({
           text-align: center;
        });
    }
    &_desc {
        color: #010101;
        font-size: 24px;
        line-height: 24px;
        margin-top: -5px;
        .xs-block({
            text-align: center;
        });
    }
    &_wrap {
        .row-flex();
        margin-top: 10px;
        .sm-block({
            justify-content: center;
        });
    }
    &_item {
        .col();
        .size(3);
        .size-md(4);
        .size-sm(6);
        .size-xs(12);
        margin: 4px;
        height: 225px;
        background-image: url(../img/move-min.jpg);
        background-size: cover;
        background-position: center center;
        position: relative;
        filter: grayscale(100%);
        transition: .3s all;
        &:hover {
            filter: none;
        }
        &--h3 {
            position: absolute;
            bottom: 15px; left: 10px;
            padding: 0 7px;
            color: #fff;
            font-size: 38px;
            line-height: 38px;
        }
    }
}

.var {
    margin-top: 25px;
    padding-top: 40px;
    background-color: #ebecc0;
    min-height: 390px;
    .lg-block({
        min-height: inherit;
        padding: 40px 0;
    });
    &_h2 {
        color: #504f53;
        font-size: 58px;
        line-height: 58px;
        .xs-block({
            text-align: center;
        });
    }
    &_desc {
        margin-top: -5px;
        color: #000000;
        font-size: 24px;
        .xs-block({
            text-align: center;
        });
    }
    &_wrap {
        margin-top: 18px;
        .row-flex();
        justify-content: space-between;
        align-items: flex-end;
        .lg-block({
            justify-content: center;
        });
    }
    &_item {
        .col();
        .size(2);
        .size-lg(3);
        .size-md(4);
        .size-sm(6);
        .size-xs(12);
        .lg-block({
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
        });
        .sm-block({
           margin: 10px 0;
        });
        &--h3 {
            margin-top: 20px;
            text-align: center;
            color: #1b1c20;
            font-size: 34px;
            .sm-block({
                font-size: 32px;
            });
        }
    }
}

