        font-size: 42px;
        line-height: 42px;
        color: @orange;
        .bold();
        .xs-block({
            font-size: 36px;
        });
    }
    &_slider {
        margin-top: 35px;
        overflow: hidden;
        padding: 0 75px;
        .slick-prev {
            background-image: url(../img/arrow.png);
            width: 55px;
            height: 55px;
            z-index: 99;
            top: 35%;
            left: 0;
            transform: rotate(0deg);
            &::before {
                content: '';
            }
        }
        .slick-next {
            background-image: url(../img/arrow.png);
            transform: rotate(-180deg);
            width: 55px;
            height: 55px;
            z-index: 99;
            right: 0;
            top: 35%;
            &::before {
                content: '';
            }
        }
    }
    &_item {
        display: flex;
        .xs-block({
            flex-direction: column;
            justify-content: center;
            text-align: center;
            align-items: center;
        });
        &--right {
            margin-left: 12px;
        }
        &--time {
            margin-top: 25px;
            color: @orange;
        }
