.footer {
    background-color: #68676c;
    padding: 30px 0 35px;
    &_wrap {
        .row-flex();
        justify-content: space-between;
        .md-block({
            justify-content: center;
        });
        .xs-block({
            text-align: center;
        });
    }
    &_item {
        .col();
        .size(2.4);
        .size-md(4);
        .size-xs(10);
        line-height: 1.4;
        color: #fff;
        &:nth-child(2),
        &:nth-child(3) {
            margin-top: 177px;
            .xs-block({
                margin-top: 35px;
            });
        }
        img {
            margin-bottom: 15px;
        }
        &:nth-child(4),
        &:nth-child(5) {
            img {
                margin-bottom: 35px;
            }
        }
        &:nth-child(4),
        &:nth-child(5) {
            .md-block({
                margin-top: 35px;
            });
        }
        .bold {
            .bold();
        }
        .inline {
            display: block;
        }
        a {
            display: inline-block;
            &:hover {
                text-decoration: underline;
            }
        }
        p {
            display: inline-block;
        }
    }
    &_bottom {
        margin-top: 15px;
        color: @orange;
        text-align: center;
    }
}