@font-face { 
    font-family: 'CompactC';
    src: url('../fonts/CompactC.eot');
    src: url('../fonts/CompactC.eot') format('embedded-opentype'),
         url('../fonts/CompactC.woff2') format('woff2'),
         url('../fonts/CompactC.woff') format('woff'),
         url('../fonts/CompactC.ttf') format('truetype'),
         url('../fonts/CompactC.svg#CompactC') format('svg');
}
.compact() {
    font-family: 'CompactC';
}
