.btn_mnu {
  display: none;
  width: 50px;
  height: 50px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  cursor: pointer;
  position: relative;
  z-index: 101;
}

.btn_row {
  height: 2px;
  width: 60%;
  margin: 2px 0;
  background-color: #fff;
  position: relative;
  transition: all .5s;
}

.btn_mnu.active .btn_row:first-child {
  transform: rotate(-135deg);
