.checkbox {
  position: relative;
  cursor: pointer;
}

.checkbox .label {
  margin-top: 3px;
  position: relative;
  display: block;
  width: 12px;
  height: 12px;
  border: 1px solid #69676c;
  background-color: #fff;
  border-radius: 100%;
}

.checkbox .label:after {
  content: '';
  position: absolute;
  top: 2px;
  left: 2px;
  width: 6px;
  height: 6px;
  border-radius: 100%;
  background: #69676c;
  transform: scale(0);
  transition: all 0.2s ease;
  opacity: 0.1;
  pointer-events: none;
}

.checkbox:hover .label:after {
  transform: scale(4.2);
}

input[type="checkbox"]:checked + .label {
  border-color: #69676c;
  animation: jelly 0.6s ease forwards;
}

input[type="checkbox"]:checked + .label:after {
  transform: scale(1);
  transition: all 0.2s ease;
  opacity: 1;
}

